<template>
    <div class="product" v-if="product">
      <div class="product-title d-flex mb-2">
        <h4>{{ product.title }}</h4>
        <button type="button" class="btn btn-outline-success ml-auto" @click.prevent="prev">Назад</button>
      </div>
      <div class="product-container d-flex mb-4">
        <div class="product-container__avatar mr-2">
          <img class="mx-auto" v-if="product.image" :src="product.image"/>
        </div>
        <div class="product-container__information text-center w-100 mt-auto">
          <p class="text-center h6 text-primary">На <strong>{{ gram }}</strong> гр. продукту</p>
          <div class="information-items d-flex">
            <div class="information flex-fill d-flex flex-column">
              <span class="information__header">БЖ</span>
              <span class="information__footer">{{ bxbjCalc(bxCalc(product.product_b), bjCalc(product.product_j))}}</span>
            </div>
            <div class="information flex-fill d-flex flex-column">
              <span class="information__header">У</span>
              <span class="information__footer">{{ byCalc(product.product_y)}}</span>
            </div>
            <div class="information flex-fill d-flex flex-column">
              <span class="information__header">ХЕ</span>
              <span class="information__footer">{{ allCalc(bxCalc(product.product_b),bjCalc(product.product_j),byCalc(product.product_y))}}</span>
            </div>
          </div>
        </div>
      </div>
      <div class="product-form">
        <form @submit="setProducts" class="mb-3">
          <div class="mb-3">
            <label for="productInput" class="form-label">Количесво грамм</label>
            <input v-model="gram" type="number" class="form-control" id="productInput" placeholder="100">
          </div>
          <div class="d-flex">
            <router-link :to="`/edit/${product._id}`" class="btn btn-success d-flex align-items-center">
              <b-icon icon="tools" aria-hidden="true"></b-icon>
            </router-link>
            <button type="submit" class="btn btn-success w-100 text-center mx-1">
              <b-icon class="mr-2" icon="house-door" aria-hidden="true"></b-icon>
              Добавить
            </button>
            <b-button class="d-flex align-items-center" variant="danger" id="popover-remove" ref="button">
              <b-icon icon="x-circle" aria-hidden="true"></b-icon>
            </b-button>
          </div>

        </form>
        <div class="d-flex">
          <b-button @click.prevent="addDishes" class="w-100" variant="info">
            Создать блюдо
          </b-button>
          <b-popover
              target="popover-remove"
              triggers="click"
              :show.sync="popoverShow"
              placement="auto"
              container="my-container"
              ref="popover"
          >
            <template #title>
              <b-button @click="onClose" class="close" aria-label="Close">
                <span class="d-inline-block" aria-hidden="true">&times;</span>
              </b-button>
              Вы уверены что хотите удалить продукт
            </template>
            <div class="d-flex">
              <b-button @click="onClose" size="sm" variant="danger" class="mr-2 w-100">Нет</b-button>
              <b-button @click="onOk" size="sm" variant="primary" class="w-100">Да</b-button>
            </div>
          </b-popover>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  name: 'Product',
  data() {
    return {
      product: null,
      gram: 100,
      popoverShow: false
    }
  },
  computed: {
    url() {
      return this.$route.params.page
    },
    isOnline() {
      return window.navigator.onLine;
    },
    productInfo() {
      return this.$store.state.productInfo;
    },
    productIds() {
      return this.$store.state.productIds;
    },
    bx_param: {
      get() {
        return this.$store.state.option.bx_param
      }
    },
    bj_param: {
      get() {
        return this.$store.state.option.bj_param
      }
    }
  },
  methods: {
    prev() {
      this.$router.back()
    },
    calculate(value) {
      return (parseFloat(value) * this.gram) / 100
    },
    bxCalc(value) {
      return ((this.calculate(value) * parseFloat(this.bx_param)) / 12).toFixed(1);
    },
    bjCalc(value) {
      return ((this.calculate(value) * parseFloat(this.bj_param)) / 12).toFixed(1);
    },
    byCalc(value) {
      return (this.calculate(value) / 12).toFixed(1);
    },
    bxbjCalc(p1, p2) {
      return (parseFloat(p1) + parseFloat(p2)).toFixed(1);
    },
    allCalc(p1, p2, p3) {
      return (parseFloat(p1) + parseFloat(p2) + parseFloat(p3)).toFixed(1);
    },
    async getProducts() {
      try {
        await this.$pouch.get(this.url).then((res) => {
          this.product = res;
          this.gram = this.product.gram
        });
      } catch (error) {
        console.error(error)
      }
    },
    async setProducts(evt) {
      evt.preventDefault();
      try {
        this.product.gram = this.gram;
        this.product.is_home = 1;
        await this.$pouch.put(this.product);
        this.prev();
      } catch (error) {
        console.error(error)
      }
    },
    addDishes() {
      this.product.dishes_gram = this.gram;
      this.$store.dispatch('setProductDishes', this.product);
    },

    //remove
    async removeProductFB() {
      try {
        await this.$firebase.database().ref().child(`product/${this.product._id}`).remove()
      } catch (error) {
        console.error(error)
      }
    },

    async onOk() {
      try {
        await this.$pouch.remove({
          _id: this.product._id,
          _rev: this.product._rev
        }).then(() => {
          if (this.isOnline) {
            this.removeProductFB().then(() => {
              this.$router.back()
            })
          } else  {
            this.$router.back()
          }
        }).catch(function (err) {
          console.log(err);
        });
      } catch (error) {
        console.error(error)
      }
    },

    onClose() {
      this.popoverShow = false
    },
  },
  mounted() {
    this.getProducts();
  }
}
</script>

<style scoped lang="scss">
.product {
  .product-container {
    &__avatar {
      width: 100px;
      min-width: 100px;
    }

    &__information {
      .information {
        margin: 0 2px;

        .information__header {
          border-bottom: 1px solid green;

        }

        .information__footer {
          font-weight: 500;
          font-size: 18px;
        }
      }
    }
  }
}
</style>
